import { Outlet } from "remix";
import { LinksFunction } from "remix";
import styleUrl from "~/styles/index.css";
export const links: LinksFunction = () => {
  return [{ rel: "stylesheet", href: styleUrl }];
};
export default function Todo() {
  return (
    <div>
      <h1>List of Todos</h1>
      <Outlet />
    </div>
  );
}
